<template>
  <div class="proauth-page">
    <div class="proauth-form">
      <vxe-input class="form-input" v-model="formData.no" placeholder="请输入编号查询"></vxe-input>
      <vxe-button class="form-button" type="button" @click="searchEvent">查询</vxe-button>
    </div>
    <div class="proauth-pager">
      <vxe-pager
        class="form-page"
        :current-page.sync="pageVO.currentPage"
        :page-size.sync="pageVO.pageSize"
        :total="pageVO.total"
        :layouts="['PrevPage', 'NextPage']"
        @page-change="pageChangeEvent">
      </vxe-pager>
    </div>
    <div class="proauth-list">
      <div class="proauth-item" v-for="(item, index) in list" :key="index" :title="`${item.no} - ${$utils.getAuthUserType(item.aType)}授权`">
        <div class="proauth-content">
          <h4 class="item-title">
            <i class="fa" :class="$utils.getAuthUserIcon(item.aType)" style="margin-right: 0.5em;"></i>
            <span>{{ item.aName }}</span>
          </h4>
          <div class="proauth-info">
            <span class="item-no">编号：{{ item.no }}</span>
            <span class="item-date">授权时间：{{ item.date }}</span>
          </div>
        </div>
      </div>
      <div v-if="!list.length" class="no-data">
        <span>可能编号不正确，找不到对应的记录！</span>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import XEAjax from 'xe-ajax'

export default {
  data () {
    return {
      loading: false,
      invalidNOs: [],
      pageVO: {
        currentPage: 1,
        pageSize: 10,
        total: 1
      },
      formData: {
        no: ''
      },
      list: []
    }
  },
  computed: {
    ...mapState([
      'loginToken',
      'loginInfo'
    ])
  },
  mounted () {
    this.searchEvent()
  },
  methods: {
    searchEvent () {
      const no = this.formData.no ? this.formData.no.trim() : ''
      if (no && !this.invalidNOs.includes(no)) {
        this.loading = true
        XEAjax.get(`/api/pub/vxetable/auth/page/list/${this.pageVO.pageSize}/${this.pageVO.currentPage}?no=${no}`).then(data => {
          this.loading = false
          const rest = this.$utils.getRest(data)
          this.list = rest.result
          this.pageVO.total = rest.page.total
        }).catch(e => {
          this.loading = false
          this.invalidNOs.push(no)
        })
      }
    },
    pageChangeEvent () {
      this.searchEvent()
    }
  }
}
</script>

<style lang="scss" scoped>
.proauth-form {
  padding: 5px 0 10px 0;
  text-align: center;
  .form-input {
    width: 400px;
    vertical-align: middle;
    & /deep/ .vxe-input--inner {
      border-radius: 4px 0 0 4px;
    }
  }
  .form-button.type--button {
    vertical-align: middle;
    margin-left: 0;
    border-radius: 0 4px 4px 0;
  }
}
.proauth-pager {
  width: 1200px;
  margin: 0 auto;
  text-align: right;
  .form-page {
    background: transparent;
  }
}
.proauth-list {
  width: 1200px;
  margin: 0 auto;
  &:after {
    content: "";
    display: block;
    clear: both;
    height: 0;
    overflow: hidden;
    visibility: hidden;
  }
  .proauth-item {
    width: 600px;
    float: left;
    width: 50%;
    padding: 10px;
  }
  .proauth-content {
    width: 100%;
    height: 100%;
    overflow: hidden;
    border-radius: 4px;
    border: 1px solid #EBEEF5;
    box-shadow: 1px 2px 10px #eee;
    background-color: #fff;
  }
  .item-title {
    margin: 0;
    padding: 20px 15px;
    border-bottom: 1px solid #EBEEF5;
    overflow: hidden;
    text-overflow: ellipsis;
    white-space: nowrap;
  }
  .proauth-info {
    padding: 20px 15px;
  }
  .item-date {
    padding-left: 20px;
  }
  .no-data {
    text-align: center;
    line-height: 2em;
  }
}
</style>
